import React from 'react';
import { View, Text, Pressable, StyleSheet, Platform } from 'react-native';
import { BaseProduct } from '../types/scraper';
import { spacing } from '../constants/spacing';

interface ComparisonControlsProps {
  comparisonMode: boolean;
  selectedFoils: BaseProduct[];
  onCompareStart: () => void;
  onCompareSelected: () => void;
  onExitCompare: () => void;
  isComparisonView?: boolean;
}

const WebWrapper = Platform.select({
  web: ({ children }: { children: React.ReactNode }) => (
    <div style={{
      position: 'fixed',
      bottom: 50,
      left: 0,
      right: 0,
      zIndex: 1000,
      pointerEvents: 'none',
    }}>
      <div style={{ pointerEvents: 'auto', display: 'flex', justifyContent: 'flex-end', paddingRight: spacing.LARGE }}>
        {children}
      </div>
    </div>
  ),
  default: ({ children }: { children: React.ReactNode }) => <>{children}</>,
});

export function ComparisonControls({
  comparisonMode,
  selectedFoils,
  onCompareStart,
  onCompareSelected,
  onExitCompare,
  isComparisonView = false,
}: ComparisonControlsProps) {
  const content = !comparisonMode ? (
    <Pressable
      style={styles.floatingButton}
      onPress={onCompareStart}
    >
      <Text style={styles.buttonText}>Compare Foils</Text>
    </Pressable>
  ) : (
    <View style={styles.comparisonBar}>
      {!isComparisonView && (
        <>
          <Text style={styles.selectionText}>
            Selected: {selectedFoils.length}
          </Text>
          <View style={styles.buttonGroup}>
            <Pressable
              style={[styles.button, selectedFoils.length < 2 && styles.buttonDisabled]}
              onPress={onCompareSelected}
              disabled={selectedFoils.length < 2}
            >
              <Text style={styles.buttonText}>Compare</Text>
            </Pressable>
          </View>
        </>
      )}
      <Pressable
        style={[styles.button, styles.cancelButton]}
        onPress={onExitCompare}
      >
        <Text style={styles.buttonText}>Back</Text>
      </Pressable>
    </View>
  );

  return Platform.OS === 'web' ? (
    <WebWrapper>{content}</WebWrapper>
  ) : (
    <View style={styles.container}>
      {content}
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    position: 'absolute',
    bottom: 50,
    left: 0,
    right: 0,
    paddingHorizontal: spacing.LARGE,
    pointerEvents: 'box-none',
  },
  comparisonBar: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'flex-end',
    gap: spacing.MEDIUM,
    backgroundColor: 'transparent',
  },
  floatingButton: {
    alignSelf: 'flex-end',
    backgroundColor: '#007AFF',
    paddingHorizontal: spacing.LARGE,
    paddingVertical: spacing.MEDIUM,
    borderRadius: 8,
    ...Platform.select({
      web: {
        boxShadow: '0px 2px 4px rgba(0,0,0,0.1)',
        cursor: 'pointer',
      },
      default: {
        elevation: 2,
        shadowColor: '#000',
        shadowOffset: { width: 0, height: 2 },
        shadowOpacity: 0.1,
        shadowRadius: 4,
      }
    })
  },
  buttonGroup: {
    flexDirection: 'row',
    gap: spacing.MEDIUM,
  },
  button: {
    backgroundColor: '#007AFF',
    paddingHorizontal: spacing.LARGE,
    paddingVertical: spacing.MEDIUM,
    borderRadius: 8,
    ...Platform.select({
      web: {
        boxShadow: '0px 2px 4px rgba(0,0,0,0.1)',
        cursor: 'pointer',
      },
      default: {
        elevation: 2,
        shadowColor: '#000',
        shadowOffset: { width: 0, height: 2 },
        shadowOpacity: 0.1,
        shadowRadius: 4,
      }
    })
  },
  buttonDisabled: {
    backgroundColor: '#ccc',
  },
  cancelButton: {
    backgroundColor: '#FF3B30',
  },
  buttonText: {
    color: 'white',
    fontWeight: '600',
    fontSize: 14,
  },
  selectionText: {
    fontSize: 14,
    color: '#666',
    marginRight: spacing.MEDIUM,
  },
}); 