import React from 'react';
import { View, Text, StyleSheet, Pressable } from 'react-native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import { useNavigation } from '@react-navigation/native';
import BrowseScreen from '../screens/BrowseScreen';
import { PrivacyPolicy } from '../screens/legal/PrivacyPolicy';
import { TermsOfService } from '../screens/legal/TermsOfService';
import { Contact } from '../screens/legal/Contact';
import { RootStackParamList } from '../types/navigation';
import { SearchBar } from '../components/SearchBar';

const Stack = createNativeStackNavigator<RootStackParamList>();

interface AppNavigatorProps {
  searchQuery: string;
  onSearch: (text: string) => void;
}

export const AppNavigator = ({ searchQuery, onSearch }: AppNavigatorProps) => {
  const navigation = useNavigation();

  const handleLogoPress = () => {
    navigation.reset({
      index: 0,
      routes: [{ name: 'Browse' }],
    });
  };

  return (
    <Stack.Navigator>
      <Stack.Screen 
        name="Browse" 
        component={BrowseScreen}
        initialParams={{ searchQuery }}
        options={{
          headerTitle: '',
          headerLeft: () => (
            <View style={styles.headerContainer}>
              <Pressable 
                onPress={handleLogoPress}
                style={({ pressed }) => [
                  styles.logoContainer,
                  pressed && styles.logoPressed
                ]}
              >
                <Text style={styles.headerTitle}>FoilQuiver</Text>
              </Pressable>
              <View style={styles.searchBarContainer}>
                <SearchBar 
                  value={searchQuery}
                  onChangeText={onSearch}
                  onClear={() => onSearch('')}
                />
              </View>
            </View>
          ),
          headerStyle: {
            backgroundColor: '#f4f4f4',
          },
        }}
      />
      <Stack.Screen 
        name="PrivacyPolicy" 
        component={PrivacyPolicy}
        options={{
          title: 'Privacy Policy',
          headerStyle: {
            backgroundColor: '#f4f4f4',
          },
        }}
      />
      <Stack.Screen 
        name="TermsOfService" 
        component={TermsOfService}
        options={{
          title: 'Terms of Service',
          headerStyle: {
            backgroundColor: '#f4f4f4',
          },
        }}
      />
      <Stack.Screen 
        name="Contact" 
        component={Contact}
        options={{
          title: 'Contact Us',
          headerStyle: {
            backgroundColor: '#f4f4f4',
          },
        }}
      />
    </Stack.Navigator>
  );
};

const styles = StyleSheet.create({
  headerContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    flex: 1,
    paddingRight: 16,
    paddingLeft: 16,
  },
  headerTitle: {
    fontSize: 20,
    fontWeight: 'bold',
    marginRight: 16,
  },
  searchBarContainer: {
    flex: 1,
  },
  logoContainer: {
    padding: 8,
    marginLeft: -8,
  },
  logoPressed: {
    opacity: 0.7,
  },
}); 