import React, { useMemo } from 'react';
import { View, Text, Pressable, ScrollView, StyleSheet, Platform, ViewStyle } from 'react-native';
import { BaseProduct } from '../types/scraper';
import { SortOption, SortField } from '../hooks/useSortedFoils';
import { tableConfig } from '../config/tableConfig';
import { MEASUREMENTS, TABLE } from '../constants/config';
import { formatMeasurement } from '../utils/dataTransform';
import { colors } from '../constants/colors';
import { spacing } from '../constants/spacing';

interface FoilsTableProps {
  foils: BaseProduct[];
  sortOption: SortOption;
  onSort: (field: SortField) => void;
  comparisonMode: boolean;
  isSelected: (foil: BaseProduct) => boolean;
  onFoilSelect: (foil: BaseProduct) => void;
}

export const FoilsTable: React.FC<FoilsTableProps> = ({
  foils,
  sortOption,
  onSort,
  comparisonMode,
  isSelected,
  onFoilSelect,
}) => {
  // Calculate max widths for each column
  const columnWidths = useMemo(() => {
    const baseWidths = {
      brand: 100,      // Base width for "Brand" + sort indicator
      series: 120,     // Base width for "Series" + sort indicator
      model: 160,      // Base width for longer model names
      wingspan: 100,   // Width for "Wingspan" + numbers + unit
      area: 100,       // Width for "Area" + numbers + unit
      aspectRatio: 80, // Width for "AR" + numbers
      cord: 90,        // Width for "Cord" + numbers + unit
      type: 80,        // Width for "Type" + sort indicator
      price: 90,       // Width for "Price" + numbers
      link: 100,       // Width for "Product Link"
    };

    // Find maximum content lengths
    foils.forEach(foil => {
      const brandWidth = (foil.brand?.length || 0) * 8 + spacing.MEDIUM * 3;
      baseWidths.brand = Math.max(baseWidths.brand, brandWidth);
      
      const seriesWidth = (foil.series?.length || 0) * 8 + spacing.MEDIUM * 3;
      baseWidths.series = Math.max(baseWidths.series, seriesWidth);
      
      const modelWidth = (foil.model?.length || 0) * 8 + spacing.MEDIUM * 3;
      baseWidths.model = Math.max(baseWidths.model, modelWidth);
    });

    return baseWidths;
  }, [foils]);

  const styles = StyleSheet.create({
    container: {
      flex: 1,
    },
    headerContainer: {
      backgroundColor: colors.LIGHT_GRAY,
      borderBottomWidth: 1,
      borderBottomColor: colors.BORDER,
      position: 'sticky',
      top: 0,
      zIndex: 1,
    },
    headerScroll: {
      flexGrow: 0,
    },
    headerRow: {
      flexDirection: 'row',
    },
    row: {
      flexDirection: 'row',
      borderBottomWidth: 1,
      borderBottomColor: colors.BORDER,
    },
    cell: {
      padding: spacing.MEDIUM,
      justifyContent: 'center',
    },
    textCell: {
      alignItems: 'flex-start',
    },
    numericCell: {
      alignItems: 'flex-end',
    },
    brandCell: {
      width: columnWidths.brand,
      flexShrink: 0,
      flexGrow: 0,
    },
    seriesCell: {
      width: columnWidths.series,
      flexShrink: 0,
      flexGrow: 0,
    },
    modelCell: {
      width: columnWidths.model,
      flexShrink: 0,
      flexGrow: 0,
    },
    wingspanCell: {
      width: columnWidths.wingspan,
      flexShrink: 0,
      flexGrow: 0,
    },
    areaCell: {
      width: columnWidths.area,
      flexShrink: 0,
      flexGrow: 0,
    },
    arCell: {
      width: columnWidths.aspectRatio,
      flexShrink: 0,
      flexGrow: 0,
    },
    cordCell: {
      width: columnWidths.cord,
      flexShrink: 0,
      flexGrow: 0,
    },
    typeCell: {
      width: columnWidths.type,
      flexShrink: 0,
      flexGrow: 0,
    },
    priceCell: {
      width: columnWidths.price,
      flexShrink: 0,
      flexGrow: 0,
    },
    linkCell: {
      width: columnWidths.link,
      flexShrink: 0,
      flexGrow: 0,
    },
    headerText: {
      fontWeight: 'bold',
      color: colors.TEXT_PRIMARY,
      flex: 1,
    },
    numericText: {
      textAlign: 'right',
      width: '100%',
    },
    headerContent: {
      flexDirection: 'row',
      alignItems: 'center',
      justifyContent: 'space-between',
      width: '100%',
    },
    headerNumericContent: {
      flexDirection: 'row',
      alignItems: 'center',
      justifyContent: 'flex-end',
      width: '100%',
      gap: spacing.MEDIUM,
    },
    sortIndicator: {
      width: 24,
      textAlign: 'center',
    },
    selectable: Platform.select<ViewStyle>({
      web: {
        cursor: 'pointer',
      },
      default: {}
    }),
    selectedRow: {
      backgroundColor: colors.SELECTED_ROW,
    },
    cellContent: {
      width: '100%',
    },
    link: {
      color: colors.LINK,
      textDecorationLine: 'underline',
    },
    headerCell: {
      ...Platform.select({
        web: {
          cursor: 'pointer',
        },
        default: {},
      }),
      backgroundColor: colors.headerBackground,
      padding: spacing.small,
      borderBottomWidth: 1,
      borderBottomColor: colors.border,
    },
    stickyHeader: {
      position: 'absolute',
      top: 0,
      left: 0,
      right: 0,
      zIndex: 1,
      backgroundColor: colors.LIGHT_GRAY,
      borderBottomWidth: 1,
      borderBottomColor: colors.BORDER,
    },
    tableBody: {
      flex: 1,
    },
  });

  const getSortIndicator = (field: SortField) => {
    if (sortOption.field !== field) return '';
    return sortOption.direction === 'asc' ? '' : '';
  };

  const renderHeaderContent = (text: string, field: SortField, isNumeric: boolean = false) => (
    <View style={isNumeric ? styles.headerNumericContent : styles.headerContent}>
      <Text style={[styles.headerText, isNumeric && styles.numericText]}>{text}</Text>
      <Text style={styles.sortIndicator}>{getSortIndicator(field)}</Text>
    </View>
  );

  const renderRow = (foil: BaseProduct) => {
    const rowKey = `row-${foil.brand}-${foil.model}`.toLowerCase().replace(/\s+/g, '-');
    const selected = isSelected(foil);
    
    return (
      <Pressable
        key={rowKey}
        onPress={() => {
          if (comparisonMode) {
            console.log('Toggling selection for:', foil.model);
            onFoilSelect(foil);
          }
        }}
        style={[
          styles.row, 
          selected && styles.selectedRow,
          comparisonMode && styles.selectable
        ]}
      >
        <View style={[styles.cell, styles.textCell, styles.brandCell]}>
          <View style={styles.cellContent}>
            <Text>{foil.brand}</Text>
          </View>
        </View>
        <View style={[styles.cell, styles.textCell, styles.seriesCell]}>
          <View style={styles.cellContent}>
            <Text>{foil.series}</Text>
          </View>
        </View>
        <View style={[styles.cell, styles.textCell, styles.modelCell]}>
          <View style={styles.cellContent}>
            <Text>{foil.model}</Text>
          </View>
        </View>
        <View style={[styles.cell, styles.numericCell, styles.wingspanCell]}>
          <View style={styles.cellContent}>
            <Text style={styles.numericText}>{formatMeasurement(foil.specs.wingspan || 0, MEASUREMENTS.LENGTH.METRIC)}</Text>
          </View>
        </View>
        <View style={[styles.cell, styles.numericCell, styles.areaCell]}>
          <View style={styles.cellContent}>
            <Text style={styles.numericText}>{formatMeasurement(foil.specs.area || 0, MEASUREMENTS.AREA.METRIC)}</Text>
          </View>
        </View>
        <View style={[styles.cell, styles.numericCell, styles.arCell]}>
          <View style={styles.cellContent}>
            <Text style={styles.numericText}>{foil.specs.aspectRatio?.toFixed(1) || '-'}</Text>
          </View>
        </View>
        <View style={[styles.cell, styles.numericCell, styles.cordCell]}>
          <View style={styles.cellContent}>
            <Text style={styles.numericText}>{formatMeasurement(foil.specs.cord || 0, MEASUREMENTS.LENGTH.METRIC) || '-'}</Text>
          </View>
        </View>
        <View style={[styles.cell, styles.textCell, styles.typeCell]}>
          <View style={styles.cellContent}>
            <Text>{foil.type.charAt(0).toUpperCase() + foil.type.slice(1)}</Text>
          </View>
        </View>
        <View style={[styles.cell, styles.numericCell, styles.priceCell]}>
          <View style={styles.cellContent}>
            <Text style={styles.numericText}>{foil.price ? `$${foil.price}` : '-'}</Text>
          </View>
        </View>
        <View style={[styles.cell, styles.textCell, styles.linkCell]}>
          <View style={styles.cellContent}>
            <Text style={styles.link} onPress={() => Platform.OS === 'web' ? window.open(foil.sourceUrl, '_blank') : null}>
              View
            </Text>
          </View>
        </View>
      </Pressable>
    );
  };

  return (
    <View style={styles.container}>
      <ScrollView horizontal>
        <View>
          <View style={styles.headerContainer}>
            <View style={styles.headerRow}>
              <Pressable style={[styles.cell, styles.textCell, styles.brandCell]} onPress={() => onSort('brand')}>
                {renderHeaderContent('Brand', 'brand')}
              </Pressable>
              <Pressable style={[styles.cell, styles.textCell, styles.seriesCell]} onPress={() => onSort('series')}>
                {renderHeaderContent('Series', 'series')}
              </Pressable>
              <Pressable style={[styles.cell, styles.textCell, styles.modelCell]} onPress={() => onSort('model')}>
                {renderHeaderContent('Model', 'model')}
              </Pressable>
              <Pressable style={[styles.cell, styles.numericCell, styles.wingspanCell]} onPress={() => onSort('wingspan')}>
                {renderHeaderContent('Wingspan', 'wingspan', true)}
              </Pressable>
              <Pressable style={[styles.cell, styles.numericCell, styles.areaCell]} onPress={() => onSort('area')}>
                {renderHeaderContent('Area', 'area', true)}
              </Pressable>
              <Pressable style={[styles.cell, styles.numericCell, styles.arCell]} onPress={() => onSort('aspectRatio')}>
                {renderHeaderContent('AR', 'aspectRatio', true)}
              </Pressable>
              <Pressable style={[styles.cell, styles.numericCell, styles.cordCell]} onPress={() => onSort('cord')}>
                {renderHeaderContent('Cord', 'cord', true)}
              </Pressable>
              <Pressable style={[styles.cell, styles.textCell, styles.typeCell]} onPress={() => onSort('type')}>
                {renderHeaderContent('Type', 'type')}
              </Pressable>
              <Pressable style={[styles.cell, styles.numericCell, styles.priceCell]} onPress={() => onSort('price')}>
                {renderHeaderContent('Price', 'price', true)}
              </Pressable>
              <View style={[styles.cell, styles.textCell, styles.linkCell]}>
                {renderHeaderContent('Product Link', 'link')}
              </View>
            </View>
          </View>
          <ScrollView style={styles.tableBody}>
            <View>
              {foils.map(renderRow)}
            </View>
          </ScrollView>
        </View>
      </ScrollView>
    </View>
  );
}; 